import { getPointsByDate, getPointsByDuration, getPointsByPrice } from './format-time.js';


const SortType = {
  DAY: 'day',
  EVENT: 'event',
  TIME: 'time',
  PRICE: 'price',
  OFFER: 'offer',
};


if(!Array.prototype.toSorted) {
  Array.prototype.toSorted = function(fn) {
    return [...this].sort(fn);
  };
}

const sort = {
  [SortType.DAY]: (events) => events.toSorted(getPointsByDate),
  [SortType.PRICE]: (events) => events.toSorted(getPointsByPrice),
  [SortType.TIME]: (events) => events.toSorted(getPointsByDuration),
  [SortType.EVENT]: () => {
    throw new Error(`  ${SortType.EVENT} `);
  },
  [SortType.OFFER]: () => {
    throw new Error(`  ${SortType.OFFER} `);
  }
};


export { SortType, sort };
